import LocalCache from './services/local-cache';
const SettingsKey = 'wdl-settings';
import { wdlHttpClient } from './services/http-service';

// eslint-disable-next-line no-unused-vars
const getSettings = async (ctx) => {
  const localSettings = LocalCache.get(SettingsKey);
  if (localSettings) return JSON.parse(localSettings) || {};

  try {
    if (localSettings) return localSettings;
    const res = await wdlHttpClient.get(`/websites/settings`);

    LocalCache.set(
      SettingsKey,
      JSON.stringify({
        lastUpdated: new Date(),
        config: res,
      })
    );
    // handle when there is no settings.config
    return res || {};
  } catch (e) {
    ctx.log(e.message);
    if (localSettings) return localSettings || {};
    // handle error here
    throw e;
  }
};

export default { getSettings };
